"use client";

import { motion } from "framer-motion";

interface PhoneProps {
  phone: string;
  setPhone: (value: string) => void;
  agreed1: boolean;
  setAgreed1: (value: boolean) => void;
  agreed2: boolean;
  setAgreed2: (value: boolean) => void;
  onSubmit: () => void;
}

export default function Phone({
  phone,
  setPhone,
  agreed1,
  setAgreed1,
  agreed2,
  setAgreed2,
  onSubmit,
}: PhoneProps) {
  return (
    <motion.div
      key="form"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      transition={{ duration: 0.5 }}
      className="w-full max-w-lg py-8 px-12 rounded-2xl shadow-sm flex flex-col justify-center bg-[var(--color-bg-neutral-50)]"
    >
      {/* Title */}
      <h2 className="text-2xl font-semibold text-[var(--color-text-primary)] mb-3 text-left">
        Let’s get started
      </h2>

      {/* Subtitle */}
      <p className="text-sm text-[var(--color-text-secondary)] mb-8 text-left">
        Enter your phone number to continue. We’ll send you a verification code.
      </p>

      {/* Form */}
      <form
        className="space-y-6"
        onSubmit={(e) => {
          e.preventDefault();
          onSubmit();
        }}
      >
        {/* Phone Input */}
        <div>
          <label
            htmlFor="phone"
            className="block text-sm font-medium text-[var(--color-text-primary)] mb-2"
          >
            Phone number
          </label>

          <div className="flex items-center border border-[var(--color-btn-teritary-200)] rounded-lg bg-[var(--color-bg-neutral-50)] focus-within:ring-2 focus-within:ring-[var(--color-primary)]">
            <span className="flex items-center pl-4 pr-3 text-[var(--color-text-secondary)] text-base font-medium">
              +1
            </span>
            <div className="h-6 w-px bg-[var(--color-btn-teritary-200)]"></div>
            <input
              type="tel"
              id="phone"
              placeholder="000 000 0000"
              className="w-full px-3 py-2 text-[var(--color-text-primary)] placeholder-[var(--color-text-muted)] bg-transparent focus:outline-none"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
            />
          </div>
        </div>

        {/* Checkboxes */}
        <div className="space-y-3">
          <label className="flex items-start space-x-2 text-xs text-[var(--color-text-secondary)]">
            <input
              type="checkbox"
              checked={agreed1}
              onChange={() => setAgreed1(!agreed1)}
              className="mt-0.5 accent-[var(--color-primary)]"
            />
            <span>
              I agree to receive Workly updates and notifications via SMS or WhatsApp.
            </span>
          </label>

          <label className="flex items-start space-x-2 text-xs text-[var(--color-text-secondary)]">
            <input
              type="checkbox"
              checked={agreed2}
              onChange={() => setAgreed2(!agreed2)}
              className="mt-0.5 accent-[var(--color-primary)]"
            />
            <span>
              I accept the{" "}
              <a href="#" className="text-[var(--color-primary)] underline">
                Terms and Conditions
              </a>
              , and Privacy Policy.
            </span>
          </label>
        </div>

        {/* Submit Button */}
        <button
          type="submit"
          className="w-full bg-[var(--color-btn-primary-500)] hover:bg-[var(--color-btn-primary-600)] text-[var(--color-secondary)] py-3 rounded-lg font-semibold transition hover:cursor-pointer"
        >
          Get Started
        </button>
      </form>
    </motion.div>
  );
}
